.menu-section ul {
    list-style: none;
    margin: 0;
    padding: 0;
    /* height: 100px; */
    /* background-color: thistle; */
}

.dish {
    height: 50px;
    color: white;
}

.header {
    padding: 50px 0 0 0;
    display: flex;
    justify-content: center;
}

.header h1 {
    color: rgb(198, 0, 0);
    text-align: center;
    font-size: 5rem;
    font-weight: 300;
    letter-spacing: 8px;
    text-shadow: 0 0 8px rgb(198, 0, 0);
    transform: translate(8px, 0);
    /* width: 200px; */
}

.image-container {
    position: sticky;
    top: 0;
    z-index: -1;
    width: 100%;
}

.image-container img {
    width: 100%;
    /* min-width: 800px; */
    position: absolute;
    top: -5vh;
    /* left: -5vw; */
    /* z-index: -100; */
    opacity: 0.25;
    /* object-fit: fill; */
}

@media (max-width: 700px) {
    .image-container img {
        top: 30vw;
    }
}