@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&family=Roboto+Flex:opsz@8..144&display=swap');

.item {
    width: 80%;
    max-width: 800px;
    margin: 100px auto;
    padding: 30px 0;
    border-radius: 4px;
}

.name {
    color: white;
    font-size: 40px;
    text-align: center;
    margin: 0 0 20px 0;
    font-family: 'Roboto Flex', sans-serif;
}

.price {
    color: rgb(198, 0, 0);
}

.description {
    color: white;
    text-align: justify;
    font-size: 24px;
    max-width: 60%;
    margin: auto;
    font-family: 'Roboto Flex', sans-serif;
}

.image {
    max-width: 40%;
    margin: auto;
}

.image img {
    width: 100%;
}

.form {
    display: flex;
    justify-content: center;
    font-size: 40px;
}

.form input {
    width: 100px;
    height: 50px;
    box-sizing: border-box;
    border: none;
    outline: none;
    font-size: 20px;
    font-family: 'Roboto Flex', sans-serif;
}

.form button {
    width: 50px;
    height: 50px;
    margin: 0 0 0 10px;
    border: none;
    outline: none;
    font-size: 20px;
    background-color: rgb(198, 0, 0);
    color: white;
}

.form button:active {
    box-shadow: 0 0 8px rgb(198, 0, 0);
}

.form button:hover, input:hover {
    cursor: pointer;
}

@media (max-width: 700px) {
    .description {
        font-size: 16px;
        max-width: 70%;
    }

    .item {
        margin: 50px auto;
    }

    .form input {
        width: 60px;
        height: 30px;
    }
    
    .form button {
        width: 30px;
        height: 30px;
        margin: 0 0 0 10px;
    }
}