@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&family=Roboto+Flex:opsz@8..144&display=swap');

.cart-button {
    border: none;
    outline: none;
    border-radius: 50%;
    background-color: rgb(198, 0, 0);
    width: 65px;
    height: 65px;
    position: sticky;
    position: -webkit-sticky;
    bottom: 25px;
    left: 25px;
}

.cart-button:active {
    box-shadow: 0 0 8px rgb(198, 0, 0);
}

.cart-button:hover {
    cursor: pointer;
}

.cart-icon {
    fill: white;
    width: 35px;
    margin: 8px 4px 0 0;
}

.cart-icon path {
    stroke-width: 0;
}

.amount {
    background-color: white;
    position: absolute;
    top: 12px;
    right: 11px;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: 'Roboto Flex', sans-serif;
    font-weight: 600;
}

@media (max-width: 700px) {
    .cart-button {
        width: 50px;
        height: 50px;
        bottom: 15px;
        left: 15px;
    }

    .cart-icon {
        width: 30px;
    }

    .amount {
        width: 15px;
        height: 15px;
        top: 8px;
        right: 8px;
        font-size: 10px;
    }
}