@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&family=Roboto+Flex:opsz@8..144&display=swap');

.close-button {
    outline: none;
    border: none;
    background-color: transparent;
    font-size: 50px;
    font-weight: bold;
    color: #000;
    float: right;
    margin: 0 10px 0 0;
}

.close-button:hover {
    cursor: pointer;
}

.container {
    margin: 70px 0 0 0;
    width: 100%;
    height: calc(100% - 70px);
    position: relative;
    font-family: 'Roboto Flex', sans-serif;
}

.list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.cart-item {
    display: flex;
    font-size: 30px;
    line-height: 30px;
    padding: 20px 0;
    border-bottom: 1px rgb(198, 0, 0) solid;
}

.controls {
    /* position: absolute;
    left: 10%; */
    margin: 0 0 0 10%;
    display: flex;
    align-items: center;
}

.remove {
    border: 1px black solid;
    outline: none;
    background-color: white;
    color: black;
    width: 30px;
    height: 30px;
}

.remove:active {
    box-shadow: 0 0 5px rgb(111, 111, 111);
}

.remove:hover {
    cursor: pointer;
}

.add {
    border: 1px black solid;
    outline: none;
    background-color: rgb(198, 0, 0);
    color: white;
    margin: 0 0 0 5px;
    width: 30px;
    height: 30px;
}

.add:active {
    box-shadow: 0 0 5px rgb(198, 0, 0);
}

.add:hover {
    cursor: pointer;
}


.name {
    /* position: absolute; */
    margin: 0 0 0 2%;
}

.amount {
    position: absolute;
    left: 55%;
    color: rgb(198, 0, 0);
}

.price {
    position: absolute;
    right: 10%;
}

.summary {
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.total {
    font-size: 25px;
}

.buy-button {
    margin: 15px;
    width: 100px;
    height: 50px;
    border: 1px black solid;
    outline: none;
    font-size: 20px;
    background-color: rgb(198, 0, 0);
    color: white;
}

.buy-button:hover {
    cursor: pointer;
}

@media (max-width: 700px) {
    .cart-item {
        font-size: 18px;
    }

    .controls {
        margin: 0 0 0 5%; 
    }

    .name {
        margin: 0 0 0 2%;
    }

    .price {
        right: 5%;
    }

    .amount {
        left: 60%;
    }
}