.backdrop {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    /* top: 0; */
    z-index: 10;
}

.content {
    top: calc(50% - 250px);
    left: calc((100% - 600px) / 2 );
    background-color: white;
    /* width: 60%; */
    width: 600px;
    height: 500px;
    position: fixed;
    /* top: 0; */
    z-index: 20;
    border-radius: 5px;
}

@media (max-width: 700px) {
    .content {
        left: 10%;
        width: 80%;
    }
}