.logo-section {
    height: 100vh;
    width: 80%;
    margin: auto;
    display: flex;
}

.logo {
    fill: transparent;
    animation: fill-anim 2.5s ease forwards 8.5s;
}

path {
    stroke-width: 1;
    stroke: white;
}

.logo path:nth-child(1) {
    stroke-dasharray: 243px;
    stroke-dashoffset: 243px;
    animation: line-anim 3s ease forwards 2s;
}

