@keyframes line-anim {
    to {
        stroke-dashoffset: 0;
    }
}

@keyframes fill-anim {
    from {
        fill: transparent;
    }
    to {
        fill: white;
    }
}

@keyframes bowl-anim {
    from {
        fill: transparent;
    }
    to {
        fill: rgb(198, 0, 0);
        filter: drop-shadow(0 0 8px rgb(198, 0, 0));
    }
}

@keyframes stick-flash {
    to {
        stroke-dashoffset: 110px;
    }
}

@keyframes stick-flash-up {
    to {
        stroke-dashoffset: 0;
    }
}

@keyframes bowl-flash {
    to {
        stroke-dashoffset: 493px;
    }
}

@keyframes bowl-flash-up {
    to {
        stroke-dashoffset: 0;
    }
}